form {
  .form-section {
    margin-top: 10px;

    .section-heading {
      margin-bottom: 20px;

      .heading-title, .heading-description {
        color: $medium-blue;
      }

      .heading-title {
        font-size: 16px;
        font-weight: bold;
      }

      .heading-description {
        margin-top: 10px;

        font-size: 13px;
      }
    }

    .form-subsection {
      + .form-subsection {
        margin-top: 10px;
      }

      .subsection-heading {
        margin: 5px 0;

        .heading-title {
          color: $medium-blue;
          font-size: 13px;
          text-transform: uppercase;

          svg {
            fill: $medium-blue;
          }
        }
      }
    }
  }

  .form-section-separator {
    position: relative;

    width: 100%;
    margin: 20px 0 10px;

    text-align: center;

    span {
      display: inline-block;

      color: grey;
      text-transform: uppercase;

      &:before, &:after {
        position: absolute;
        top: 4px;

        width: 235px;

        content: ' ';

        border-top: solid 1px;
      }

      &:before {
        left: 0;

        border-image: linear-gradient(to left, $medium-grey, white) 1;
      }

      &:after {
        left: 265px;

        border-image: linear-gradient(to right, $medium-grey, white) 1;
      }
    }
  }
}
